@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
:root {
  --primary: #c36f69;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */

body {
  font-family: "Noto Serif";
  color: #4e4e4e;
  color: var(--secondary);
  background-color:white
}

.keyboard {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  width: 60%;
  padding: 5px 0;
  margin: 250px auto;
  background: black;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.5);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: bottom 0.4s;
}

.keyboardKeys {
  text-align: center;
}

.keyboardKey {
  height: 45px;
  width: 6%;
  max-width: 90px;
  margin: 3px;
  border-radius: 4px;
  border: none;
  background: rgba(255, 255, 255, 0.2);
  color: #ffffff;
  font-size: 1.45rem;
  outline: none;
  cursor: pointer;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  vertical-align: top;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  position: relative;
}

.keyboardKey:active {
  background: rgba(255, 255, 255, 0.12);
}

.keyboardKey--wide {
  min-width: 15%;
  max-width: 15%;
}

.keyboardKey--extra-wide {
  width: 36%;
  max-width: 500px;
}

.active {
  background: #00ff38;
  color: black;
}

.dFlex {
  display: -webkit-flex;
  display: flex;
}

.inputBox {
  width: 60%;
  font-size: 25px;
  font-weight: 500;
  height: 150px;
  padding: 12px 20px;
  margin: 50px auto;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.header {
  -webkit-justify-content: center;
          justify-content: center;
  margin: 30px 0px;
}
